.mainController {
	position: fixed;
    top: 60px;
    right: 18px;
    width: 250px;
    height: 250px;
    padding: 10px;
    background: #ececec;
    border: 1px solid #aaa;
    box-shadow: 0px 3px 3px 0px #aaa;
    
    input{
    	width: calc(100% - 30px);
	    margin: 10px 0px;
	    height: 20px;
	    border: 1px solid #aaa;
	    padding: 0px 5px;
	    background: #eaeaea;
    }
   
   button {
   	background: none;
    border: 1px solid #aaa;
    outline: none;
    box-shadow: none;
    padding: 5px;
    cursor: pointer;
   }
  
   button.closeButton {
   	position: absolute;
    top: 20px;
    border: none;
    height: 22px;
    line-height: 22px;
    font-size: 15px;
    padding: 0px 10px;	
   }
  
  section{
  	margin-bottom: 10px;
  }
}


.app-content-wrapper {
	@keyframes spin {
	    0% { transform: rotate(0deg); }
	    100% { transform: rotate(360deg); }
	}

	.loader-wrapper {
	    position: fixed;
	    height: 100%;
	    width: 100%;
	    top: 0;
	    left: 0;
	    background: #0000006b;	
	    
	    .loader {
			position: absolute;
		    top: 50%;
		    left: 50%;
		    transform: translate(-50%,-50%);
		    border: 5px solid #fff;
		    border-radius: 50%;
		    border-top: 5px solid #069;
		    width: 26px;
		    height: 26px;
		    -webkit-animation: spin 2s linear infinite;
		    animation: spin 2s linear infinite;
		}
	}

	
}